import React, { useEffect } from 'react';
import cover from '../../../img/cover/cover-java.webp';
// @ts-ignore
import Prism from 'prismjs';
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/plugins/line-numbers/prism-line-numbers.js';
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';

const Types = () => {
    useEffect(() => Prism.highlightAll(), []);

    return (
        <>
            <div className={'cover-wrapper'}>
                <img src={cover} alt={'html cover'} className={'cover'} />
            </div>
            <article className={'article article-content'}>
                <p>Metoda statyczna moe mie argumenty takie jak zwyka metoda lub nie mie adnych argumentw. Jednak
                    w przeciwiestwie do zwykych metod, metody statyczne maj kilka specjalnych cech:</p>
                <ul>
                    <li>metoda statyczna moe uzyska dostp tylko do pl statycznych, nie moe uzyska dostpu do pl
                        niestatycznych (z wntrza metody statycznej nie moemy odwoywa si do pl i metod nie
                        zadeklarowanych jako statyczne)
                    </li>
                    <li>metoda statyczna moe wywoa inn metod statyczn, ale nie moe wywoa metody instancji</li>
                    <li>metoda statyczna nie moe odwoywa si do sowa kluczowego <span
                        className={'snippet'}>this</span>, poniewa w
                        kontekcie statycznym nie ma instancji
                    </li>
                </ul>
                <p>Zwyke metody mog jednak uzyska dostp do pl i metod statycznych. Dodatkowo metody i zmienne
                    zadeklarowane jako <span className={'snippet'}>static</span> zwizane s z konkretn klas, a nie
                    jej instancj -
                    obiektem. <span className={'snippet'}>static</span> oznacza, e co istnieje zawsze. Nie trzeba
                    tworzy instancji
                    klasy, eby wywoa metod statyczn. Na przykad, zamiast:
                </p>
                <pre className={'line-numbers language-js'}>
                    <code>
{`package sample;

public class Sample {
    public static void main(String[] args) {
        Matematyka test = new Matematyka();
        
        double wynik = test.dodaj(10, 20);
        
        System.out.println(wynik);
    }
}

class Matematyka {
    double dodaj(double a, double b) {
        return a + b;
    }
}`}
                    </code>
                </pre>

                <p>mona zastosowa <span className={'snippet'}>static</span> i zmieni na co takiego:</p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`package sample;

public class Sample {
    public static void main(String[] args) {
    // Matematyka test = new Matematyka();
    
    double wynik = Matematyka.dodaj(10, 20);

    System.out.println(wynik);
    }
}

class Matematyka {
    static double dodaj(double a, double b) {
        return a + b;
    }
}`}
                    </code>
                </pre>
            </article>
        </>
    );
};

export default Types;
