import React, { useEffect } from 'react';
import cover from '../../../img/cover/cover-java.webp';
// @ts-ignore
import Prism from 'prismjs';
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/plugins/line-numbers/prism-line-numbers.js';
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';
import 'prismjs/components/prism-java';

const Types = () => {
    useEffect(() => Prism.highlightAll(), []);

    return (
        <>
            <div className={'cover-wrapper'}>
                <img src={cover} alt={'html cover'} className={'cover'} />
            </div>
            <article className={'article article-content'}>
                <h1>4 paradygmaty programowania obiektowego</h1>
                <h3>Dziedziczenie</h3>
                <p>Jest mechanizmem, ktry umoliwia tworzenie nowych klas na bazie klas ju istniejcych (rozszerzajc
                    ich moliwoci). Klasa, ktra dziedziczy po innej klasie, przejmuje jej cechy (pola) i zachowania
                    (metody) oraz dodaje wasne metody i pola, ktre su przystosowywaniu do innych zada. Czsto
                    obiekty s bardzo podobne, wic dziedziczenie umoliwia programistom ponowne wykorzystanie wsplnej
                    logiki i jednoczesne wprowadzenie do klas unikalnych koncepcji.</p>
                <h4>Co dziedziczymy</h4>
                <ul className={'snippet-list'}>
                    <li>zachowania</li>
                    <li>cechy</li>
                    <li>kada klasa niejawnie dziedziczy po klasie <span>Object</span> (chyba, e zdefiniujesz
                        inn klas po ktrej dziedziczysz)
                    </li>
                </ul>

                <h4>Co dziedziczymy z klasy Object</h4>
                <ul>
                    <li>metoda <span>hashCode()</span></li>
                    <li>metoda <span>equals()</span></li>
                    <li>metoda <span>toString()</span></li>
                    <li>metoda <span>wait()</span></li>
                    <li>metoda <span>notify()</span></li>
                    <li>etc.</li>
                </ul>

                <h4>Czego nie dziedziczymy</h4>
                <ul className={'snippet-list'}>
                    <li>konstruktorw</li>
                    <li>klas i metod <span>final</span></li>
                </ul>
                <p>Zdarzaj si sytuacje, kiedy chcemy, aby nie tworzono podklas jednej z klas. Klasy, ktrych nie mona
                    rozszerza, nazywaj si klasami finalnymi (<span>final</span>). Finalna moe
                    by te metoda w klasie. W takim przypadku nie mona jej przesoni w adnej z podklas (wszystkie
                    metody w klasie finalnej s finalne). Rwnie pola mog by finalne. Warto takiego pola nie moe
                    by zmieniana po utworzeniu obiektu. Jeli klasa jest finalna, to tylko jej metody s finalne, nie
                    dotyczy to pl.
                </p>

                <h3>Abstrakcja</h3>
                <p>Oznacza, e obiekty powinny udostpnia uproszczon, abstrakcyjn wersj swoich implementacji
                    (tworzymy uproszczony model wiata). Szczegy ich pracy wewntrznej zwykle nie s potrzebne
                    uytkownikowi, wic nie ma potrzeby ich reprezentowania. Abstrakcja oznacza rwnie, e zostan
                    przedstawione tylko najbardziej istotne cechy obiektu.
                </p>
                <p>Klasa abstrakcyjna to klasa, w ktrej umieszcza si wsplny kod dla wszystkich klas dziedziczcych po
                    tej klasie abstrakcyjnej (o ile same nie s abstrakcyjne). Nie da si te stworzy instancji klasy
                    abstrakcyjnej.
                    <details>
                        <summary>Przykad</summary>
                        <p>Pojciem abstrakcyjnym jest np. rodek transportu. rodkiem transportu moe by zarwno
                            samolot jak i samochd. Zarwno samolot jak i samochd porusza si z pewn prdkoci, a
                            wic prdko jest wsplnym parametrem. Idc dalej samochd moe by osobowy lub ciarowy.
                            Kady z nich posiada cechy wsplne jak np. spalanie czy waga. Kade z tych poj jest
                            abstrakcyjne, poniewa posiada pewne cechy wsplne dla grupy oraz cechy ukryte, czyli te,
                            ktrymi si rni. Idc dalej samochody ciarowe moemy przykadowo podzieli ze wzgldu
                            na mark lub ilo osi. Idc tym tokiem rozumowania naley si zastanowi gdzie koczy si
                            abstrakcja. Prawdopodobnie nigdzie, zawsze znajd si cechy wsplne i cechy ktrymi dane
                            elementy si rni co pozwala nam zdefiniowa kolejne poziomy abstrakcji.</p>
                    </details>
                </p>

                <h3>Polimorfizm</h3>
                <p className={'snippets'}>To moliwo odwoywania si przez obiekty do wielu rnych typw. Kady
                    obiekt podklasy jest obiektem nadklasy, np. kady kierownik jest pracownikiem, wic
                    klasa <span>Manager</span> moe by podklas klasy <span>Employee</span>. Nie mona tego odwrci -
                    nie kady pracownik jest kierownikiem. Wszdzie tam, gdzie mona uy obiektu nadklasy, mona uy
                    obiektu podklasy. Polimorfizm oznacza dosownie jedno imi i wiele form i dotyczy dziedziczenia
                    klas. Jak sama nazwa wskazuje, pozwala programistom definiowa rne logiki tej samej metody. Tak
                    wic nazwa (lub interfejs) pozostaje taka sama, ale wykonywane czynnoci mog by inne. W praktyce
                    odbywa si to z przecieniem lub nadpisaniem.
                </p>
                <details>
                    <summary>Przykad</summary>
                    <pre className={'line-numbers language-java'}>
                        <code>
{`@Getter
public class Employee {
    private String name;
    private String surname;
    private int age;
}`}
                        </code>
                    </pre>

                    <pre className={'line-numbers language-java'}>
                        <code>
{`public class Manager extends Employee {
    private List<Employee> team = new ArrayList<>();
}`}
                        </code>
                    </pre>

                    <pre className={'line-numbers language-java'}>
                        <code>
{`public class Main {
    public static void main(String[] args) {
        Employee employee1 = new Employee();
        Employee employee2 = new Manager();

        String name = employee2.getName();
    }
}`}
                        </code>
                    </pre>
                </details>

                <h3>Hermetyzacja</h3>
                <p>Ukrywanie implementacji (enkapsulacja), aby nie byo do nich bezporedniego dostpu z innych klas.
                    Zapewnia, e obiekt nie moe zmienia stanu wewntrznego innych obiektw w nieoczekiwany sposb.
                    Caa interakcja z obiektem i jego danymi odbywa si za pomoc jego metod publicznych. Tylko wasne
                    metody obiektu s uprawnione do zmiany jego stanu. Do enkapsulacji su modyfikatory dostpu.
                    Najlepiej jest kiedy pola metody s prywatne, a metody publiczne.
                </p>

                <hr />

                <h1>SOLID</h1>
                <p>Zasady SOLID stosuje si po to, aby kod mona byo atwiej rozwija i utrzymywa.</p>

                <p><strong>S</strong> - <i>Single responsibility</i> (Zasada pojedynczej odpowiedzialnoci): Klasa lub
                    metoda powinna mie tylko jedn odpowiedzialno (nigdy nie powinien istnie wicej ni jeden powd
                    do modyfikacji klasy bd metody).</p>

                <p><strong>O</strong> - <i>Open/closed</i> (Zasada otwarte na rozszerzenia i zamknite na modyfikacje):
                    Powinna by moliwo rozszerzania klasy bd metody bez jej modyfikacji. Sprowadza si do do
                    wiadomego uycia kompozycji, dziedziczenia czy modyfikatorw dostpu.</p>
                <details>
                    <summary>Przykad</summary>
                    <p>Tylko jak kod moe spenia obydwa warunki zasady open/closed? Jak moe by otwarty na
                        rozbudow, a zarazem zamknity na zmiany? Poprzez uycie <strong>abstrakcji</strong>, ktra
                        bdzie wyraa stae wsplne zachowanie. To co bdzie si zmienia trafi do klas
                        konkretnych. W Javie abstrakcj moemy wyrazi albo przez klas abstrakcyjn albo przez
                        interfejs.</p>
                    <p>Przykad naruszenia zasady otwarte-zamknite:</p>
                    <pre className={'line-numbers language-java'}>
                           <code>
{`package pl.javadeveloper.solid.ocp.bad;

import java.nio.file.Files;
import java.nio.file.Paths;
import java.nio.file.StandardOpenOption;
import java.util.Arrays;

public class LoggerNonOCP {
    private LogTarget logTarget;

    public LoggerNonOCP(LogTarget logTarget) {
        this.logTarget = logTarget;
    }

    public void log(String message) throws Exception {
        switch (logTarget) {
            case CONSOLE:
                System.out.println(message);
                break; 
            case FILE:
                Files.write(Paths.get('file.log'),
                Arrays.asList(message),
                StandardOpenOption.APPEND);
                break;
            default:
                throw new IllegalArgumentException('Unsupported logging type!');
        }
    }
}`}
                            </code>
                        </pre>

                    <pre className={'line-numbers language-java'}>
                            <code>
{`package pl.javadeveloper.solid.ocp.bad;

public enum LogTarget {
    CONSOLE, FILE
}`}
                            </code>
                        </pre>

                    <p className={'snippets'}>Typ ten okrela miejsce logowania komunikatw i jest uywany w
                        metodzie <span>log</span>. Metoda ta posiada jeden parametr i jest nim logowany komunikat. W
                        zalenoci od wartoci typu wyliczeniowego trafia on albo na konsole albo do pliku. Jednak
                        wymagania klienta mog si zmieni i tym samym mog pojawia si nowe sposoby logowania. Na
                        przykad komunikaty mog by zapisywane w bazie danych. Aby doda nowy sposb logowania trzeba
                        "otworzy" kod czyli go zmieni. Ponadto, bdzie trzeba doda nowy typ logowania
                        w <span>LogTarget</span>. Taki kod narusza zasad open/closed i w konsekwencji nie pozwala na
                        rozbudow bez jego zmiany. Jeli chcemy poprawi ten kod i zapewni jego zgodno z zasad
                        open/closed musimy wydzieli abstrakcj. Bdzie ni interfejs <span>MessageLogger</span>, ktry
                        bdzie wsplny dla wszystkich sposobw logowania:
                    </p>

                    <pre className={'line-numbers language-java'}>
                            <code>
{`package pl.javadeveloper.solid.ocp.good;

public interface MessageLogger {
    void log(String message) throws Exception;
}`}
                            </code>
                        </pre>

                    <p className={'snippets'}>Kady ze sposobw logowania umiecimy w odrbnej klasie. Pierwsz z klas
                        bdzie klasa <span>ConsoleLogger</span>, ktra bdzie logowa komunikaty na konsole.
                    </p>
                    <pre className={'line-numbers language-java'}>
                            <code>
{`package pl.javadeveloper.solid.ocp.good;

public class ConsoleLogger implements MessageLogger {
    @Override
    public void log(String message) {
        System.out.println(message);
    }
}`}
                            </code>
                        </pre>

                    <p>Druga z klas czyli <span>FileLogger</span> bdzie logowaa dane do
                        pliku.</p>
                    <pre className={'line-numbers language-java'}>
                            <code>
{`package pl.javadeveloper.solid.ocp.good;

import java.nio.file.Files;
import java.nio.file.Paths;
import java.nio.file.StandardOpenOption;
import java.util.Arrays;

public class FileLogger implements MessageLogger {
    @Override
    public void log(String message) throws Exception {
        Files.write(Paths.get('file.log'),
        Arrays.asList(message),
        StandardOpenOption.APPEND);
    }
}`}
                            </code>
                        </pre>

                    <p>Po zmianach gwna klasa bdzie wygldaa nastpujco:</p>
                    <pre className={'line-numbers language-java'}>
                            <code>
{`package pl.javadeveloper.solid.ocp.good;

public class LoggerOCP {
    private MessageLogger messageLogger;

    public LoggerOCP(MessageLogger messageLogger) {
        this.messageLogger = messageLogger;
    }

    public void log(String message) throws Exception {
        messageLogger.log(message);
    }
}`}
                            </code>
                        </pre>

                    <p className={'snippets'}>Klasa ma pole typu <span>MessageLogger</span>. Podczas jej tworzenia w
                        konstruktorze do tego pola przypisujemy konkretn implementacje sposobu logowania.
                        Metoda <span>log</span> staa si teraz bardzo prosta. Pozostao w niej tylko wywoanie
                        metody <span>log</span> z obiektu klasy przekazanej w konstruktorze. Teraz moemy bardzo atwo
                        doda kolejne sposoby logowania i to bez wpywu na gwn klas programu. W tym przypadku
                        zachowujemy zgodno z zasad otwarte zamknite. Kod jest otwarty na rozbudow, ale zamknity na
                        zmiany.
                    </p>
                    <p>Przykadem moe by rwnie uycie wzorca Strategy.</p>
                </details>

                <p><strong>L</strong> - <i>Liskov substitution</i> (Zasada podstawienia Liskov): Klasy w programie
                    powinny by podmienialne przez swoje podklasy bez naruszania poprawnoci programu, czyli klasa
                    dziedziczca musi by dobrym odpowiednikiem klasy bazowej. Innymi sowy jeli zaleysz od jakiego
                    interfejsu to wszystkie jego implementacje powinny poprawnie dziaa z Twoj klas/metod. Po lewej
                    stronie przypisania powinien znajdowa si najbardziej oglny typ pozwalajcy realizowa nasz
                    funkcj.
                </p>
                <details>
                    <summary>Przykad</summary>
                    <p>W tym przypadku wietnym przykadem s kolekcje w jzyku Java. Po lewej piszemy <span
                        className={'snippet'}>List</span> a po prawej np. <span>ArrayList</span>
                    </p>
                    <pre className={'line-numbers language-java'}>
                       <code>
{`public class SubstitutionExample {
    public static void main(String[] args) {
        List&ltString&gt someList = new ArrayList<>();
        Set<String> someSet = new HashSet<>();
        Queue<String> someQueue = new PriorityQueue<>();
        SubstitutionExample example = new SubstitutionExample();
        example.doSomethingWithElements(someList);
        example.doSomethingWithElements(someSet);
        example.doSomethingWithElements(someQueue);
    }

    public void doSomethingWithElements(Collection<String> someCollection) {
        for (String element : someCollection) {
            System.out.println('element: ' + element);
        }
    }
}`}
                        </code>
                    </pre>

                    <p className={'snippets'}>Metoda <span>doSomethingWithElements</span> zrobi dokadnie to samo bez
                        wiedzy o tym, z jakim podtypem ma do czynienia. Niezalenie od tego czy bdzie
                        to <span>ArrayList</span> czy <span>PriorityQueue</span> metoda zadziaa poprawnie.
                    </p>
                </details>

                <p><strong>I</strong> - <i>Interface segregation</i> (Zasada segregacji interfejsw): Wiele mniejszych,
                    konkretnych interfejsw jest lepsze od pojedynczego oglnego interfejsu. Klasa ktra implementuje
                    interfejs nie moe by zmuszana do implementowania metod, ktrych nie potrzebuje, a tak jest czsto
                    w przypadku duych iterfejsw.
                    <details>
                        <summary>Przykad</summary>
                        <p>Zamy, e w interfejsie s 3 metody:</p>
                        <pre className={'line-numbers language-java'}>
                            <code>
{`public interface ObjectFormatter {
    byte[] toPDF(Object someObject);

    String toXML(Object someObject);

    String toJSON(Object someObject);
}`}
                            </code>
                        </pre>

                        <p>Interfejs ten jest uywany w trzydziestu innych projektach. W kadym przypadku uywa
                            wycznie jednej z tych trzech metod. Niestety z jakiego powodu trzeba zmieni ten
                            interfejs. W konsekwencji kady z tych projektw musi wprowadzi jakie zmiany. W praktyce
                            rozdzielenie tego interfejsu na trzy oddzielne moe mie sens:
                        </p>
                        <pre className={'line-numbers language-java'}>
                            <code>
{`public interface PDFFormatter {
    byte[] toPDF(Object someObject);
}`}
                            </code>
                        </pre>

                        <pre className={'line-numbers language-java'}>
                            <code>
{`public interface XMLFormatter {
    String toXML(Object someObject);
}`}
                            </code>
                        </pre>

                        <pre className={'line-numbers language-java'}>
                            <code>
{`public interface JSONFormatter {
    String toJSON(Object someObject);
}`}
                            </code>
                        </pre>

                        <p>W takim przypadku zmiana jednej z tych metod nie pociga za sob zmian w kadym z 30
                            wspomnianych projektw.</p>
                    </details>
                </p>

                <p><strong>D</strong> - <i>Dependency inversion</i> (Zasada odwrcenia zalenoci): Klasy nadrzdne nie
                    mog zalee od podrzdnych. Zaleno ta powinna by odwrcona poprzez wprowadzenie dodatkowych
                    elementw. Mwi si tu o dodatkowych warstwach abstrakcji, ktre pozwalaj na zmian kierunku
                    takiej zalenoci.</p>
                <details>
                    <summary>Przykad</summary>
                    <p className={'snippets'}>Rozwamy przykad klasy do zarzdzania zadaniami (naruszenie zasady
                        odwracania zalenoci). Klasa <span>TaskService</span> uywa konkretnej
                        klasy <span>FileRepository</span>, ktra zapisuje lub usuwa zadania z pliku. W tym przykadzie
                        klasa <span>TaskService</span> jest "moduem wysokiego poziomu".
                        Klasa <span>FileRepository</span> peni rol "moduu niszego poziomu". Mamy tutaj bezporedni
                        zaleno midzy klasami. W ten sposb jest naruszona zasada Dependency inversion.
                    </p>
                    <pre className={'line-numbers language-java'}>
                        <code>
{`public class TaskService {
    private FileRepository repository = new FileRepository();

    public void addTask(Task task) {
        repository.saveTask(task);
    }

    public void removeTask(String taskId) {
        repository.deleteTask(taskId);
    }
}`}
                        </code>
                    </pre>

                    <p className={'snippets'}>Aby rozwiza powyszy problem powinnimy sprawi by
                        klasa <span>TaskService</span> nie bya zalena od klasy <span>FileRepository</span>. Ponadto
                        obie klasy musz zalee od abstrakcji. Stwrzmy wic abstrakcj w postaci
                        interfejsu <span>Repository</span>. Bdzie on mia metody zwizane z zapisem i odczytem zada.
                    </p>
                    <pre className={'line-numbers language-java'}>
                        <code>
{`public interface Repository {
    void saveTask(Task task);

    void deleteTask(String taskId);
}`}
                        </code>
                    </pre>

                    <p className={'snippets'}>Zmiemy klas <span>TaskService</span> aby uywaa
                        interfejsu <span>Repository</span> i w ten sposb zaleaa od abstrakcji.
                    </p>
                    <pre className={'line-numbers language-java'}>
                        <code>
{`public class TaskService {
    private Repository repository;

    public TaskService(Repository repository) {
        this.repository = repository;
    }

    public void addTask(Task task) {
        repository.saveTask(task);
    }

    public void removeTask(String taskId) {
        repository.deleteTask(taskId);
    }
}`}
                        </code>
                    </pre>

                    <p className={'snippets'}>Rwnie klasa <span>FileRepository</span> bdzie zaleaa od abstrakcji i
                        speniaa zaoenia interfejsu <span>Repository</span>.
                    </p>
                    <pre className={'line-numbers language-java'}>
                        <code>
{`public class FileRepository implements Repository {
    @Override
    public void saveTask(Task task) {
        // logic responsible for saving task to file
    }

    @Override
    public void deleteTask(String taskId) {
        // logic responsible for deleting task from file
    }
}`}
                        </code>
                    </pre>

                    <p>Teraz "modu wysokiego poziomu" nie zaley od "moduu niskiego poziomu". Modu warstwy niszej
                        zaley od abstrakcyjnego interfejsu z warstwy wyszej. Zatem zmiany w module na niszym poziomie
                        nie wpywaj na modu na wyszym poziomie. Jeli na przykad pojawi si potrzeba zapisu zada w
                        bazie danych zamiast w pliku to czeka nas proste zadanie. Wystarczy dodanie odpowiedniej klasy
                        na niszym poziomie. Oto przykad zarysu takiej klasy dla bazy MySQL.
                    </p>
                    <pre className={'line-numbers language-java'}>
                        <code>
{`public class MySqlRepository implements Repository {
    @Override
    public void saveTask(Task task) {
        // store task in TASK table
    }

    @Override
    public void deleteTask(String taskId) {
        // delete task from TASK table
    }
}`}
                        </code>
                    </pre>
                </details>
            </article>
        </>
    );
};

export default Types;
