import React, {useEffect} from 'react';
import cover from '../../../img/cover/cover-javascript.webp';
// @ts-ignore
import Prism from 'prismjs';
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/plugins/line-numbers/prism-line-numbers.js';
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';

const SetTimeoutSetInterval = () => {
    useEffect(() => Prism.highlightAll(), []);

    return (
        <>
            <div className={'cover-wrapper'}>
                <img src={cover} alt={'html cover'} className={'cover'}/>
            </div>
            <article className={'article article-content'}>
                <h1>setTimeout() / setInterval()</h1>
                <span className={'snippet'}>setTimeout()</span> jest wykonywany dopiero po okrelonym czasie:
                <pre className={'line-numbers language-js'}>
                        <code>
{`function welcome(name) {
    console.log(name + ", welcome to our website! You have a special discount today!");
}

let timerId = setTimeout(welcome, 5000, "Mary")

clearTimeout(timerId);`}
                    </code>
                </pre>

                <span className={'snippet'}>setInterval()</span> wywouje funkcj ponownie i ponownie po okrelonym
                czasie:
                <pre className={'line-numbers language-js'}>
                        <code>
{`function alarm(time) {
    console.log("Wake up! It's " + time + " o'clock!");
}

const intervalId = setInterval(alarm, 3000, 8);

clearInterval(intervalId);`}
                    </code>
                </pre>

                <p>Obie funkcje przyjmuj te parametry:</p>
                <ul>
                    <li>funkcja do wykonania</li>
                    <li>interwa (w ms), w ktrym funkcja bdzie uruchamiana za kadym razem</li>
                    <li>argumenty funkcji do wywoania</li>
                </ul>

                <p className={'snippets'}>Po uruchomieniu licznika czasu przegldarka wykonuje zadanie w nieskoczono,
                    a tym samym zuywa zasoby. Aby tego unikn, powinnimy zatrzyma niepotrzebne timery. Uruchamiasz
                    timer <span>setTimeout()</span> lub <span>setInterval()</span>, a aby go zatrzyma, powiniene uy
                    odpowiednio <span>clearTimeout()</span> lub <span>clearInterval()</span>. W takim przypadku
                    powiniene zna identyfikator timera, ktry chcesz
                    zatrzyma. <span>setTimeout()</span> i <span>setInterval()</span> zwraca identyfikator timera, ktry
                    zosta utworzony.
                </p>
            </article>
        </>
    );
};

export default SetTimeoutSetInterval;
