import React, {useEffect} from 'react';
import cover from '../../../img/cover/cover-javascript.webp';
// @ts-ignore
import Prism from 'prismjs';
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/plugins/line-numbers/prism-line-numbers.js';
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';

const VarLet = () => {
    useEffect(() => Prism.highlightAll(), []);

    return (
        <>
            <div className={'cover-wrapper'}>
                <img src={cover} alt={'html cover'} className={'cover'}/>
            </div>
            <article className={'article article-content'}>
                <h1>var vs. let</h1>
                <p>We wczeniejszych wersjach JavaScript zmienne byy deklarowane jako <span
                    className={'snippet'}>var</span>. Jednak dzisiaj ta metoda jest uwaana za przestarza. Rnice:
                </p>
                <ol className={'snippet-list'}>
                    <li>Zmienna zadeklarowana za pomoc <span>var</span> jest dostpna wewntrz caej funkcji i
                        posiada <strong>function scope</strong>. Natomiast <span>let</span> ma <strong>block
                            scope</strong>, wic zmienne zadeklarowane za jego pomoc s dostpne wewntrz bloku kodu
                        midzy nawiasami klamrowymi.
                    </li>
                    <pre className={'line-numbers language-js'}>
                        <code>
{`function someFunc() {
    var i;
    for (i = 0; i &lt;= 5; i++) {
        var someVar = i * i;
    }
    
    console.log(i);
    console.log(someVar);
}
    
someFunc(); // 6
            // 25`}
                        </code>
                    </pre>

                    <pre className={'line-numbers language-js'}>
                        <code>
{`function someFunc() {
    let i;
    for (i = 0; i &lt;= 5; i++) {
        let someVar = i * i;
    }
    
    console.log(i);
    console.log(someVar);
}
    
someFunc(); // ReferenceError: someVar is not defined`}
                        </code>
                    </pre>

                    <li>Zmienne <span>var</span> moemy ponownie deklarowa, co jest niemoliwe w
                        przypadku <span>let</span> i <span>const</span>.
                    </li>
                    <pre className={'line-numbers language-js'}>
                    <code>
{`var name = "Micha";
var name = "Karol";
console.log(name); //Karol

let name = "Micha";
let name = "Karol"; //bd = Identifier "name" has already been declared
console.log(name);`}
                    </code>
                </pre>

                    <li>Kolejna rnica midzy starsz deklaracj a jej modszymi brami to tak
                        zwany <strong>hoisting</strong>. JavaScript lubi pomaga programicie. Jednym z takich
                        przypadkw pomocy jest niewidoczne dla programisty wynoszenie deklaracji funkcji i zmiennych na
                        pocztek danego zasigu (kodu lub funkcji). Deklaracja zmiennej <span>var</span> (bez wartoci)
                        wynoszona jest automatycznie na pocztek danego kodu (a w zasadzie na pocztek danego zakresu -
                        np. na pocztek danej funkcji). W przypadku let/const hoisting take istnieje, ale nie jestemy
                        w stanie uywa zmiennych przed ich zadeklarowaniem.
                    </li>
                    <pre className={'line-numbers language-js'}>
                        <code>
{`var a; //js przenis tutaj deklaracj zmiennej ale bez jej wartoci!
console.log(a); //wypisze undefined, ale bdu nie ma

var a = 20;`}
                        </code>
                    </pre>

                    <pre className={'line-numbers language-js'}>
                        <code>
{`console.log(a); //Error: Cannot access "a" before initialization

let a = 20;`}
                        </code>
                    </pre>

                    <li>Ostatni rnic - do mao znan - jest to, e deklarujc zmienn
                        globaln <span>var</span> (poza ciaem funkcji), dodawana jest ona jako waciwo obiektu
                        window. W przypadku let nic takiego si nie dzieje:
                    </li>
                    <pre className={'line-numbers language-js'}>
                        <code>
{`var a = 20;
let b = 30;

console.log(window.a); //20
console.log(window.b); //undefined`}
                        </code>
                    </pre>
                </ol>
            </article>
        </>
    );
};

export default VarLet;
