import React, { useEffect } from 'react';
import cover from '../../../img/cover/cover-java.webp';
// @ts-ignore
import Prism from 'prismjs';
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/plugins/line-numbers/prism-line-numbers.js';
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';
import 'prismjs/components/prism-java';

const HashCode = () => {
    useEffect(() => Prism.highlightAll(), []);

    return (
        <>
            <div className={'cover-wrapper'}>
                <img src={cover} alt={'html cover'} className={'cover'} />
            </div>
            <article className={'article article-content'}>
                <h1>hashCode</h1>
                <p>Moe suy jako kryterium porwnania dla elementw w zbiorach. Dla przykadu w zbiorze mamy dwa
                    obiekty przyjmujce jako parametr PESEL:</p>
                <pre className={'line-numbers language-java'}>
                    <code>
{`Set<Osoba> zbior = new HashSet<>();
Osoba a = new Osoba ("90012311122")
Osoba b = new Osoba ("90012311122")`}
                    </code>
                </pre>

                <p className={'snippets'}>W rzeczywistoci jest to ta sama osoba. Domylnym kryterium porwnania jest
                    hashCode (generowany na podstawie miejsca w pamici) - metoda <span>equals()</span> <strong>domylnie
                        porwnuje ich</strong> <span>hashCode</span>, czyli <strong>referencje</strong> (to, jakie
                    miejsce w pamici zajmuj). Poniewa s to dwie instancje, bd miay rny hashCode, a poniewa
                    jest to ta sama osoba, wynik porwnania powinien zwrci warto <span>true</span>. Aby to osign
                    trzeba nadpisa metody <span>equals()</span> i <span>hashCode()</span>.
                </p>
                <pre className={'line-numbers language-java'}>
                    <code>
{`@Override
public boolean equals(Object o) {
    if (this == o) return true;
    if (!(o instanceof Osoba)) return false;
    Osoba osoba = (Osoba) o;
    return Objects.equals(pesel, osoba.pesel);
}

@Override
public int hashCode() {
    return Objects.hash(pesel);
}`}
                    </code>
                </pre>

                <p className={'snippets'}>Wwczas <span>hashCode</span> s generowane na podstawie pl, w tym przypadku
                    PESEL. Metod generuje IntelliJ. Inaczej mwic, aby
                    metoda <span>equals</span> porwnujca <span>hashCode</span> obiektw daa taki sam wynik, musi mie
                    do porwnania dwa takie same <span>hashCode</span>, ktre bd takie same kiedy zostan wygenerowane
                    na podstawie pl.
                </p>
                <p className={'snippets'}>Po hashCode dokonywane jest porwnanie w HashMap czy HashSet (we wszystkim co
                    ma Hash w nazwie) czy obiekty s unikalne. Jedyn klas, ktra ma nadpisan
                    metod <span>equals</span> jest <span>String</span>. Porwnuje ona domylnie zawarto (tre)
                    stringa. Wszystkie inne klasy normalnie porwnuj ze sob referencje.
                </p>

                <div className={'video-container'}>
                    <iframe src={'https://www.youtube.com/embed/IsVnZVfCr50'} width={'100%'} height={'315'}
                        allowFullScreen title={'srcset and sizes attributes'}
                        allow={'accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture'}
                    />
                </div>
            </article>
        </>
    );
};

export default HashCode;
